.about {
    padding: 50px 10%;
    background-color: #fff3e0;
    color: #bf360c;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Лёгкая тень */
  }
  
  .heading {
    font-size: clamp(2rem, 5vw, 3rem); /* Динамический размер шрифта */
    text-align: center;
    margin-bottom: 20px;
    color: #00796b;
  }
  
  .subheading {
    font-size: clamp(1.5rem, 4vw, 2.5rem); /* Динамический размер шрифта */
    margin-top: 30px;
    margin-bottom: 10px;
    color: #00796b;
  }
  
  .paragraph {
    font-size: clamp(1rem, 2.5vw, 1.25rem); /* Динамический шрифт для текста */
    line-height: 1.6;
    margin-bottom: 20px;
    color: #333;
  }
  
  .list {
    font-size: clamp(1rem, 2.5vw, 1.25rem); /* Динамический шрифт для списка */
    line-height: 1.6;
    margin-bottom: 20px;
    color: #333;
    padding-left: 20px;
  }
  
  ul {
    padding-left: 40px; /* Дополнительные отступы для списка */
  }
  