.contact-section {
    padding: 50px 20px;
    background-color: #e3f2fd; /* Светлый голубой фон */
    color: #1a237e; /* Тёмно-синий текст */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Лёгкая тень */
  }
  
  .contact-heading {
    font-size: 2.5rem;
    text-align: center;
    color: #0d47a1;
    margin-bottom: 20px;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .contact-form label {
    font-size: 1.2rem;
    font-weight: bold;
    color: #1a237e;
  }
  
  .contact-form input,
  .contact-form textarea {
    padding: 15px;
    border: 2px solid #64b5f6; /* Светло-голубая граница */
    border-radius: 8px; /* Закругленные поля ввода */
    font-size: 1.1rem;
    background-color: #ffffff;
    transition: border-color 0.3s ease; /* Плавная анимация при наведении */
  }
  
  .contact-form input:focus,
  .contact-form textarea:focus {
    border-color: #0d47a1; /* Темно-синяя граница при фокусе */
  }
  
  .contact-form textarea {
    resize: none;
    height: 150px;
  }
  
  /* Поле для ввода номера телефона */
  .contact-form input[type="tel"] {
    padding: 15px;
    border: 2px solid #64b5f6;
    border-radius: 8px;
    font-size: 1.1rem;
    background-color: #ffffff;
    transition: border-color 0.3s ease;
  }
  
  /* Стили для кнопки отправки */
  .contact-form button {
    padding: 15px;
    font-size: 1.2rem;
    background-color: #1a237e;
    color: white;
    border: none;
    border-radius: 12px; /* Закругленная кнопка */
    cursor: pointer;
    transition: background-color 0.3s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Тень для кнопки */
  }
  
  .contact-form button:hover {
    background-color: #0d47a1; /* Более тёмный синий при наведении */
  }
  
  .contact-form button:active {
    background-color: #002171; /* Еще более тёмный синий при нажатии */
  }
  
  /* Сообщение об успешной отправке */
  .success-message {
    text-align: center;
    font-size: 1.2rem;
    color: #2e7d32; /* Зеленый цвет для сообщения об успехе */
    margin-top: 20px;
    background-color: #e8f5e9;
    padding: 15px;
    border-radius: 8px; /* Закругленный блок уведомления */
  }
  
  /* Адаптивные стили для маленьких экранов */
  @media (max-width: 600px) {
    .contact-heading {
      font-size: 2rem;
    }
  
    .contact-form {
      gap: 15px;
    }
  
    .contact-form input,
    .contact-form textarea {
      padding: 10px;
    }
  
    .contact-form button {
      padding: 12px;
      font-size: 1.1rem;
    }
  }
  