/* Основные стили секции Hero */
.hero {
    height: calc(100vh - 80px); /* Высота экрана минус header */
    background-color: #e0f7fa;
    color: #00796b; /* Зеленый цвет текста */
    padding: 3%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  /* Контейнер для слайдера */
  .slick-slider {
    max-width: 100%; /* Ограничиваем ширину слайдера */
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  /* Стили для каждого слайда */
  .slick-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    height: 100%;
    box-sizing: border-box;
    padding: 0 20px;
  }
  
  /* Изображения на слайде */
  .slick-slide img {
    max-width: 90%; /* Увеличиваем ширину изображения */
    max-height: 70vh; /* Ограничиваем высоту изображений */
    object-fit: contain; /* Сохраняем пропорции изображения */
    margin: 3% auto 0; /* Добавляем отступ сверху, чтобы header не перекрывал */
    display: block;
    border: 2px;
    border-radius: 1%;
  }
  
  /* Заголовок на слайде */
  .title {
    font-size: 2.2rem;
    font-weight: bold;
    margin: 20px 0 10px 0;
    color: #00796b;
    text-align: start;
  }
  
  /* Подзаголовок на слайде */
  .subtitle {
    font-size: 1.25rem;
    margin: 10px 0;
    padding: 10px;
    color: #00796b;
    word-wrap: break-word;
    max-width: 80%;
    text-align: start;
  }
  
  /* Адаптивные стили для мобильных устройств */
  @media (max-width: 600px) {
    .title {
      font-size: 1.5rem; /* Уменьшаем размер заголовка на маленьких экранах */
    }
  
    .subtitle {
      font-size: 1rem; /* Уменьшаем размер подзаголовка */
      padding: 15px;
    }
  
    .slick-slide img {
      max-width: 90%; /* Увеличиваем ширину изображения на маленьких экранах */
      max-height: 50vh; /* Ограничиваем высоту изображений */
      margin-top: 30%; /* Дополнительный отступ сверху */

    }
  }
  
  /* Медиа-запрос для экранов с высотой меньше 500px */
@media (max-height: 450px) {
  .slick-slide {
    position: relative; /* Для работы с текстом поверх изображения */
  }

  .slick-slide img {
    max-width: 90%; /* Увеличиваем ширину изображения */
    max-height: 50vh; /* Ограничиваем высоту изображений */

    margin: 8% auto 0; /* Добавляем отступ сверху, чтобы header не перекрывал */
    display: block;
  }

  .textContainer {
    position: absolute;
    top: 55%; /* Центрируем текст */
    left: 50%;
    transform: translate(-50%, -50%);
    color: white; /* Цвет текста, чтобы был читаем поверх картинки */
    background-color: rgba(209, 209, 209, 0); /* Полупрозрачный фон для лучшей читаемости */
    padding: 1px;
    width: 100%; /* Ограничиваем ширину текста */
    text-align: center;
    word-wrap: inherit;
    
  }
}