.products {
    padding: 50px 20px;
    background-color: #f1f8e9;
    color: #33691e;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Лёгкая тень */
  }
  
  .heading {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .productGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
  
  .productCard {
    padding: 20px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 8px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .productImage {
    width: 100%;
    max-width: 800px;
    height: 600px;
    object-fit: contain; /* Сохраняет соотношение сторон и обрезает при необходимости */
    border-radius: 4px;
    margin-bottom: 15px;
  }
  
  /* Адаптивные стили для экранов меньше 900px */
  @media (max-width: 900px) {
    .productGrid {
      grid-template-columns: 1fr;
    }
  
    .productImage {
      height: auto; /* Позволяет изображению адаптироваться к ширине */
      max-width: 100%;
    }
  }

  .productCard:hover {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    transform: translateY(-5px);
    transition: all 0.3s ease;
  }
  
  